<template lang="pug">
    v-card 
        v-card-text
            v-card-title
                span 添加定投策略
            v-container
                v-row 
                    v-col(cols="12" sm="6" lg="4")
                        v-text-field(outlined hide-details v-model="tarCoin" label="数字币")
                    v-col(cols="12" sm="6" lg="4")
                        v-text-field(outlined hide-details v-model="baseCoin" label="基础币")
                    v-col(cols="12" sm="6" lg="4")
                        v-text-field(outlined hide-details v-model="sepminutes" label="下单周期(分钟)")
                    v-col(cols="12" sm="6" lg="4")
                        v-text-field(outlined hide-details v-model="onehandmoney" label="一手买入金额(元)")
                    v-col(cols="12" sm="6" lg="4")
                        v-text-field(outlined hide-details v-model="maxmoneyonecycle" label="最大买入金额(元)")
                    v-col(cols="12" sm="6" lg="4")
                        v-text-field(outlined hide-details v-model="maxdownratio" label="触发买入幅度")
                    v-col(cols="12" sm="6" lg="4")
                        v-text-field(outlined hide-details v-model="sepseconds" label="刷新间隔(秒)")
                    
            v-card-actions
                v-btn(color="success" dark @click="save") 添加
</template>

<script>
import * as api from "@/apis/wswebapi"
import { get} from 'vuex-pathify'
export default {
    data: () => ({
    tarCoin: "btc",
    baseCoin: "bitcny",
    sepminutes:1440,
    onehandmoney:15,
    maxmoneyonecycle:50,
    maxdownratio:0.05,
    sepseconds:5,
    ExName:"WKJ",
    }),
    computed: {
    isLogin:get('user/islogin'),
    exbotisexpire:get('user/exbotisexpire'),
    username:get('user/username'),
    email:get('user/email'),
    },
    methods:{
        save(){
            if (!this.isLogin){
                this.$store.dispatch("msglog/push_msg",{message:"用户未登陆，请登陆后重试",type:"error"});
                return
            }
            if (this.exbotisexpire){
                this.$store.dispatch("msglog/push_msg",{message:"软件已过期，请联系管理员",type:"error"});
                return
            }
            let params = {
                "Username": this.username,
                "ExName": this.ExName,
                "TarCoin": this.tarCoin,
                "BaseCoin": this.baseCoin,
                "sepminutes": Number(this.sepminutes),
                "onehandmoney":Number(this.onehandmoney),
                "maxmoneyonecycle": Number(this.maxmoneyonecycle),
                "maxdownratio": Number(this.maxdownratio),
                "NotifyEmail": this.email,
                "SepSeconds": Number(this.sepseconds)
            }
            api.DTMPostOne(params).then(res=>{
                if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"});
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        }
    }
}
</script>